:root{--mainfont-nanum: "nanumSquareNeo";--mainfont-nexon: "nexonLv1";--black: #000;--black100: #000;--black200: #000;--black300: #000;--black400: #000;--black500: #000;--white: #fff;--white100: #fff;--white200: #fff;--white300: #fff;--white400: #fff;--white500: #fff;font-family:var(--mainfont-nanum),var(--mainfont-nexon);font-size:18px;line-height:1.5;font-weight:400;font-synthesis:none;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-webkit-text-size-adjust:100%}@media(max-width: 1200px){:root{font-size:16px;line-height:1.4}:root input{font-size:16px}}@media(max-width: 1000px){:root{font-size:14px;line-height:1.4}:root input{font-size:14px}}@media(max-width: 800px){:root{font-size:12px;line-height:1.4}:root input{font-size:12px}}@media(max-width: 600px){:root{font-size:10px;line-height:1.4}:root input{font-size:10px}}*,*:before,*:after{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}body,button,dd,dl,dt,fieldset,form,h1,h2,h3,h4,h5,h6,input,legend,li,ol,p,select,table,td,textarea,th,ul,figure,figcaption{margin:0;padding:0}body,button,input,select,table,textarea{font-family:var(--mainfont-nanum),var(--mainfont-nexon),"Apple SD Gothic Neo","Malgun Gothic","맑은 고딕",helvetica,sans-serif}a,a:hover,a:focus{color:inherit;text-decoration:none}h1,h2,h3,h4,h5,h6{font-weight:normal}ul{list-style:none}em,address{font-style:normal}strong{font-weight:normal}img{vertical-align:top}input,textarea,button,select{-webkit-appearance:none;-moz-appearance:none;appearance:none;border-radius:0;font-size:18px;border:0}#skip a{position:absolute;left:10px;top:-92px;z-index:100000;font-size:1rem;padding:10px 30px;color:var(--white);background:var(--black)}#skip a:focus,#skip a:active{top:10px}.blind{position:absolute;width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0, 0, 0, 0);white-space:nowrap;border:0}.ir{display:block;overflow:hidden;font-size:0;line-height:0;text-indent:-9999px;white-space:nowrap}body{background-color:var(--black);color:var(--white)}.App{width:100%;background:url(../../image/backgroundImage.png) no-repeat center;background-size:cover}.App .AppCover{width:100%;height:100%;background-color:rgba(0,0,0,.5)}.App .AppCover .Wrapper{margin:0 auto;width:1200px}@media only screen and (max-width: 1300px){.App .AppCover .Wrapper{margin:0 auto;width:90%}}#Header{padding:4rem 0 3rem;border-bottom:1px solid #fff}#Header .title{display:flex;justify-content:space-between;align-items:flex-end}#Header .title div h1{margin-bottom:.5rem;font-weight:bolder}#Header .title h2{font-weight:bolder}#Header .currentLocalWeather{margin-top:3rem;padding:3rem;display:flex;justify-content:space-between;align-items:center;border:1px solid #fff;border-radius:2rem}#Header .currentLocalWeather p h2{margin-top:.5rem;font-weight:bolder}#Header .currentLocalWeather p h2 span{color:#d36100;font-size:2rem}#Header .currentLocalWeather img{width:80px}@media only screen and (max-width: 800px){#Header .currentLocalWeather img{width:60px}}@media only screen and (max-width: 700px){#Header .title{flex-direction:column;align-items:flex-start;gap:1rem}}@media only screen and (max-width: 500px){#Header .currentLocalWeather img{width:40px}}#Main{margin-top:3rem}#Main .searchBox h2{font-weight:bolder}#Main .searchBox form{position:relative;margin-top:1rem;width:100%}#Main .searchBox form input{padding:.5rem 1.5rem;width:100%;border-radius:1rem;background-color:#fff;opacity:.5}#Main .searchBox .keyword{margin-top:1rem;padding:0 1.5rem;display:flex;gap:1.5rem}#Main .searchBox .keyword ul{padding-left:1.5rem;display:flex;gap:1.5rem;border-left:2px solid #fff}#Main .searchInfo{margin-top:1.5rem;display:flex;justify-content:space-between;align-items:center;gap:1rem}#Main .searchInfo li{flex:1;padding:2rem 1.5rem;border-radius:1rem;display:flex;justify-content:space-between;align-items:center}#Main .searchInfo li div:nth-of-type(1) h3{font-weight:bolder}#Main .searchInfo li div:nth-of-type(1) p{font-weight:bolder}#Main .searchInfo li div:nth-of-type(2){width:70px;height:70px}#Main .searchInfo li div:nth-of-type(2) img{width:100%;height:100%}#Main .searchInfo li:nth-of-type(1){background:linear-gradient(120deg, rgb(255, 233, 149) 0%, rgb(255, 255, 255) 100%);color:#d36100}#Main .searchInfo li:nth-of-type(2){background:linear-gradient(120deg, rgb(182, 217, 255) 0%, rgb(255, 255, 255) 100%);color:#004ca1}#Main .searchInfo li:nth-of-type(3){background:linear-gradient(120deg, rgb(180, 175, 255) 0%, rgb(255, 255, 255) 100%);color:#0a00bb}#Main .searchChart{margin-top:3rem;padding:2.5rem;border-radius:2rem;background-color:rgba(0,0,0,.5)}#Main .searchChart h2{font-weight:bolder}#Main .forecastWeather{margin-top:3rem}#Main .forecastWeather h2{font-weight:bolder}#Main .forecastWeather ul{margin-top:1rem;padding:1rem;display:flex;flex-direction:column;gap:1rem;background-color:rgba(0,0,0,.5);border-radius:1rem}#Main .forecastWeather ul li{padding:.5rem 1rem;display:flex;justify-content:space-between;align-items:center;gap:2rem}#Main .forecastWeather ul li div{display:flex;align-items:center;width:120px}#Main .forecastWeather ul li div img{width:30px}#Main .forecastWeather ul li p{flex:1}@media only screen and (max-width: 900px){#Main .searchInfo{flex-direction:column}#Main .searchInfo li{width:100%}#Main .searchInfo li div:nth-of-type(2){width:50px;height:50px}#Main .searchInfo li div:nth-of-type(2) img{width:100%;height:100%}#Main .forecastWeather ul li{padding:1.5rem 1rem;flex-direction:column;align-items:flex-start;gap:1rem;border-bottom:1px solid #6e6e6e}#Main .forecastWeather ul li div{display:flex;justify-content:space-between;align-items:center;width:100%}#Main .forecastWeather ul li div img{width:30px}#Main .forecastWeather ul li:last-child{border:none}}#Footer{margin-top:4rem;padding:2rem 0;width:100%;display:flex;justify-content:center;align-items:center;border-top:1px solid #fff;text-align:center}